.variant{
    stroke:rgba(0,255,154,0.6);
    stroke-width:1px;
}
body{
    background:#F4F3F3 !important;
    overflow-y: auto;
}
a:focus {
    outline:0 !important;
}

.active {
    z-index: 1;
}
.brush .extent{
    stroke: #fff;
    fill-opacity: .125;
    shape-rendering: crispEdges;
}


.point {
    fill: #2f225d;
    stroke: #afa2dc;
}

.selected {
    fill: #afa2dc;
    stroke: #2f225d;
}

.clear-button {
    font: 14px sans-serif;
    cursor: pointer;
}

.axis path,
.axis line {
    fill: none;
    stroke: #000;
    shape-rendering: crispEdges;
}

.axis {
    font: 10px sans-serif;
}
.d3-tip {
    line-height: 1;
    font-weight: bold;
    padding: 12px;
    background: rgba(0, 0, 0, 0.8);
    color: #fff;
    border-radius: 2px;
}

/*Creates a small triangle extender for the tooltip */
.tooltip2:after {
    box-sizing: border-box;
    display: inline;
    font-size: 10px;
    width: 100%;
    line-height: 1;
    color: rgba(0, 0, 0, 0.8);
    content: "\25BC";
    position: absolute;
    text-align: left;
    margin: -1px 0 0 0;
    top: 98%;
    left: 10px;
}
.tooltip3:after {
    box-sizing: border-box;
    display: inline;
    font-size: 10px;
    width: 100%;
    line-height: 1;
    color: rgba(0, 0, 0, 0.8);
    content: "\25BC";
    position: absolute;
    text-align: right;
    margin: -1px 0 0 0;
    top: 98%;
    right: 10px;
}
.yaxis{
    background-color:green;
}
.tableHighlight {
    background-color:rgba(197, 0, 99, 0.2) !important;
}

#navAside a{
    color:#D0D0D0 !important;
    padding:5px 5px 5px 30px;
}
#navAside a:hover{
    color:#E4E4E4 !important;
    background-color:#666666 !important;
}
#navAside li.active{
    border-left:5px solid #C50063!important;
}
#navAside li.active a{
    color:#E4E4E4 !important;
    background-color:#666666 !important;
}
.menu-group {
    background-color: inherit !important;
    border: none !important;
    box-shadow: none !important;
}
.menu-header {
    background-color: #777 !important;
    border: none !important;
    border-radius: 0px !important;
    border-bottom: 1px solid #656565 !important;
    padding:0px !important;
}
.menu-header h4 {
    font-size:20px;
    font-weight:300;
    text-shadow: -2px 0px 1px #666;
    font-family: HelveticaLight;
    letter-spacing: 1px;
}
.menu-header a{

    color:#F8F8F8 !important;
    padding:auto !important;
    position: relative;
    display: block;
    padding: 5px 15px 10px;
    text-decoration: none !important;
}
.menu-header a:hover{
    text-decoration: none;
    color:#E4E4E4 !important;
    background-color:#666666 !important;
}
.menu-header li.active{
    border-left:5px solid #C50063!important;
}
.menu-header li.active a{
    color:#E4E4E4 !important;
    background-color:#666666 !important;
}
.menu-content {
    border: none !important;
}
.menu-content div{
    border: none !important;
}
.prot-menu {
    border-top: 1px solid #C50063 !important;
    border-bottom: 1px solid #C50063 !important;
}
.gene-menu {
    border-top: 1px solid #0F8292 !important;
    border-bottom: 1px solid #0F8292 !important;
}
.ref-menu {
    border-top: 1px solid #CFB915 !important;
    border-bottom: 1px solid #CFB915 !important;
}
.prot-menu li.active{
    border-left: 5px solid #C50063 !important;
}
.gene-menu li.active {
    border-left: 5px solid #0F8292 !important;
}
.ref-menu li.active {
    border-left: 5px solid #CFB915 !important;
}
.menu-content li {
    background-color: inherit !important;
    border: none !important;
}
.menu-content li.active {
    -moz-transition: border-left 200ms linear !important;
    -webkit-transition: border-left 200ms linear !important;
    transition: border-left 200ms linear !important;
}
.menu-content li.active a{
    color:#333 !important;
    background-color:#F8F8F8 !important;
    box-shadow: inset -10px 0px 40px 0px #F4F3F3;
}
.menu-content a {
    color:#777 !important;
    padding:6px 10px 5px !important;
    vertical-align: bottom;
}
.menu-content a:hover {
    color:#333 !important;
    background-color:#F8F8F8 !important;
    -moz-transition: color,background 500ms ease;
    -webkit-transition: color,background 500ms ease;
    transition: color,background 500ms ease;
}
.isoSelected{
    fill:#74EB78 !important;
}
.evidenceNumber{
    background:#7CBA0F;
    padding:3px 7px;
    color:white;
    vertical-align:top;
    cursor: pointer;
}
.detailed-info{
    display:none;
}
.detailed-info p{
    margin:0px !important;
}
.evidenceArrow{
    -ms-transform: rotate(180deg); /* IE 9 */
    -webkit-transform: rotate(180deg); /* Chrome, Safari, Opera */
    transform: rotate(180deg);
    display:inline-block;
    font-weight:900;
}
.evidenceCodeName{
    background-color: #7cba0f;
    color:white;
    padding:2px 6px;
    border-radius: 2px;
    margin-left:5px;
}
.evidenceLine{
    margin:10px;
}
.evidenceSource{
    background-color: #dedede;
    color:#999;
    padding:2px 6px;
}

@font-face {
    font-family: "HelveticaUltraLight";
    src: url('../font/HelveticaNeue-UltraLight.otf');
}
@font-face {
    font-family: "HelveticaLight";
    src: url('../font/HelveticaNeue-Light.otf');
}
main {
}

#result {
    min-height: 400px;
    background-color: #ffffff;
}

.nomarginshift{
    margin-left:0px;
    margin-right:0px;
}
.fullHeight{
    min-height: 800px;
}
.sidemenu{
    background-color: #E7E7E7;
}

.entrySummaryBoxes > div {
    text-align: center;
    margin-top:8px;
    margin-left:20px;
    display: inline-block;}

.entrySummaryBoxes p{
    color:grey;
    font-size:0.8em;
    font-weight:600;
    margin-top:5px;
    margin-bottom: 5px;
}
#entrySummaryBoxName {
    background:#777777;
    height:50px;
    padding:0px;
    border-radius:0px;
    border-bottom: 1px solid #595959;
    box-shadow: inset 0px -3px 6px -6px #D0D0D0, 0px 3px 6px -6px #757575;
}
#entrySummaryName {
    color:#F8F8F8;
    font-size:1.5em;
    font-family:HelveticaUltraLight;
    height:40px;
    font-weight:500;
    padding:5px 0px 25px;
    margin-bottom:15px;
    text-align: center;
    letter-spacing:0px;
}

.entrySummaryBoxes span{color:white;font-weight:900;font-size:13px;}

#entrySummaryIso {background-color:lightgrey;padding:4px 8px;}
#entrySummaryGene {background-color:#0F8292;padding:4px 3px;}
#entrySummaryRef {background-color:#CFB915;padding:4px 3px;}

.entrySummarylink {
    display:inline-block;
    margin-top:13px;
    vertical-align:top;
    margin-right:25px;
}

.disable-link {
    font-size: 12px;
    pointer-events: none;
    cursor: default;
    padding-bottom: 0px !important;
    color:darkgray !important;
}